@use '/src/styles/constants/colors';

.dropdownDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 20rem;
  height: fit-content;
}

.dropdownTop {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 12rem;
  height: 2rem;
  padding: 0.2rem 0.3rem;
  background-color: colors.$GRAYE;
  border: 1px solid colors.$GRAYA;
  border-radius: 0.3rem;

  .dropdownTopSpan {
    width: 8rem;
    padding: 0 0.2rem;
    overflow: hidden;
    font-size: 0.8rem;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  &:hover {
    cursor: pointer;
  }
}

.dropdownBottom {
  width: 12rem;
  height: fit-content;
  margin-top: 0.5rem;
  overflow: hidden;
  background-color: colors.$WHITE;
  border: 1px solid colors.$GRAYA;
  border-radius: 0.3rem;
  box-shadow: 0 0 0.5rem colors.$BLACK20P;

  .searchInput {
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid colors.$GRAYA;

    input {
      width: 9rem;
      padding: 0.6rem 1rem 0.6rem 0;
      font-size: 0.9rem;
      color: colors.$GRAY3;
      border: none;
      border-top-left-radius: 0.3rem;
      border-top-right-radius: 0.3rem;

      &:focus {
        outline: none;
      }
    }
  }

  .selectList {
    height: 10rem;
    padding: 0;
    margin: 0;
    overflow-y: scroll;

    button {
      width: 12rem;
      padding: 0.6rem 1rem 0.6rem 2rem;
      overflow: hidden;
      font-size: 0.8rem;
      color: colors.$GRAY6;
      text-align: left;
      text-overflow: ellipsis;
      white-space: nowrap;
      background-color: colors.$WHITE;
      border: none;
      transition: background-color 0.2s;
    }

    button:hover {
      cursor: pointer;
      background-color: colors.$GRAYE;
    }
  }
}

.isHidden {
  display: none;
}

.dropdownSvgs {
  width: 1rem;
  padding: 0.5rem;
  fill: colors.$GRAY6;
}
